import React, { useEffect, useState } from 'react';
import useSWR from 'swr';
import { useFormik } from 'formik';
import { object, string } from 'yup';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';
import { CountryDropdown, RegionDropdown } from 'react-country-region-selector';
import editAccountInformations from '../../../../api/account/editAccountInformations';
import { fetcher } from '../../../../api/http';
import Loading from '../../../Elements/Loading';
import { config } from '../../../../config/config';

const form = object({
    society: string().nullable(),
    address: string().required(''),
    city: string().required(''),
    firstname: string().required(''),
    lastname: string().required(''),
    postalcode: string()
        .required('')
        .matches(/^\d{5}(?:[-\s]\d{4})?$/, 'The postal code is not correct.'),
});
export default function EditAccountInfosForm({
    setAddress,
}: {
  setAddress?: React.Dispatch<React.SetStateAction<boolean>>;
}) {
    const [loading, setLoading] = useState(false);
    const [country, setCountry] = useState('');
    const [region, setRegion] = useState('');
    const [error, setError] = useState('');

    const {
        data,
        error: erros,
        isLoading,
        mutate,
    } = useSWR(`${config.privateapilink}/account/getinfos`, fetcher);
    useEffect(() => {
        async function fetchRegion(data: {
      country_name: any;
      data: {
        country: React.SetStateAction<string>;
        country_name: React.SetStateAction<string>;
        region: React.SetStateAction<string>;
        data: { region: string };
      };
      region: any;
    }) {
            const response = await fetch('https://ipapi.co/json/');
            const responseData = await response.json();
            setCountry(
                responseData.country_name && data.data.country === ''
                    ? responseData.country_name
                    : data.data.country !== ''
                        ? data.data.country
                        : ''
            );
            setRegion(
                responseData.region && data.data.region === ''
                    ? responseData.region
                    : data.data.region !== ''
                        ? data.data.region
                        : ''
            );
        }
        if (data && !erros && !isLoading) {
            formik.setValues({
                society: data.data.society,
                address: data.data.address ? data.data.address : '',
                city: data.data.city ? data.data.city : '',
                postalcode: data.data.postal_code ? data.data.postal_code : '',
                lastname: data.data.lastname ? data.data.lastname : '',
                firstname: data.data.firstname ? data.data.firstname : '',
            });
            setCountry(data.data.country);
            setRegion(data.data.region);
            fetchRegion(data);
        }

    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [data]);

    const handleCountryChange = (value: React.SetStateAction<string>) => {
        setCountry(value);
    };

    const handleRegionChange = (value: React.SetStateAction<string>) => {
        setRegion(value);
    };

    const formik = useFormik({
        initialValues: {
            society: '',
            address: '',
            city: '',
            postalcode: '',
            firstname: '',
            lastname: '',
        },
        validationSchema: form,
        onSubmit: (values) => {
            setLoading(true);
            if (region === '' || !region) {
                setError('You need to select a State/Region');
                setLoading(false);
                return;
            }
            if (country === '' || !country) {
                setError('You need to select a Country');
                setLoading(false);
                return;
            }
            setError('');
            editAccountInformations(
                values.society,
                values.address,
                values.city,
                region,
                values.postalcode,
                country,
                values.firstname,
                values.lastname
            )
                .then((data) => {
                    if (data.data['status'] === 'error') {
                        toast.error(data.data['message'], {
                            position: 'bottom-right',
                            autoClose: 5000,
                            hideProgressBar: false,
                            closeOnClick: true,
                            pauseOnHover: true,
                            draggable: true,
                            progress: undefined,
                            theme: (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) ? 'dark' : 'light',
                        });
                        setLoading(false);
                    } else {
                        mutate();
                        toast.success('Success! Your informations was edited.', {
                            position: 'bottom-right',
                            autoClose: 5000,
                            hideProgressBar: false,
                            closeOnClick: true,
                            pauseOnHover: true,
                            draggable: true,
                            progress: undefined,
                            theme: (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) ? 'dark' : 'light',
                        });
                        setLoading(false);
                        if (setAddress) {
                            setAddress(false);
                        }
                    }
                    setLoading(false);
                })
                .catch(() => {
                    toast.error(
                        'An unexcepted error happend. Please contact one of our support team.',
                        {
                            position: 'bottom-right',
                            autoClose: 5000,
                            hideProgressBar: false,
                            closeOnClick: true,
                            pauseOnHover: true,
                            draggable: true,
                            progress: undefined,
                            theme: (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) ? 'dark' : 'light',
                        }
                    );
                    setLoading(false);
                });
        },
    });
    if (!data || error || isLoading) {
        return <Loading />;
    }
    return (

        <form
            onSubmit={formik.handleSubmit}
            className="bg-white shadow-sm ring-1 ring-gray-900/5 sm:rounded-xl md:col-span-2"
        >
            <div className="px-4 py-6 sm:p-8">
                <div
                    className={
                        error === ''
                            ? 'hidden alert alert-warning shadow-lg'
                            : 'flex my-4 alert alert-warning shadow-lg'
                    }
                >
                    <div>
                        <svg
                            xmlns="http://www.w3.org/2000/svg"
                            className="stroke-current flex-shrink-0 h-6 w-6"
                            fill="none"
                            viewBox="0 0 24 24"
                        >
                            <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth="2"
                                d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
                            />
                        </svg>
                        <span>{error}</span>
                    </div>
                </div>
                <div className="grid max-w-2xl grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                    <div className="sm:col-span-3">
                        <label htmlFor="first-name" className="block text-sm font-medium leading-6 text-gray-900">
                First name
                        </label>
                        <div className="mt-2">
                            <input
                                id="firstname"
                                name="firstname"
                                type="text"
                                onChange={formik.handleChange}
                                disabled={loading}
                                required
                                defaultValue={data.data.firstname ? data.data.firstname : ''}
                                autoComplete="given-name"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            />
                        </div>
                    </div>

                    <div className="sm:col-span-3">
                        <label htmlFor="last-name" className="block text-sm font-medium leading-6 text-gray-900">
                Last name
                        </label>
                        <div className="mt-2">
                            <input
                                autoComplete="family-name"
                                id="lastname"
                                name="lastname"
                                type="text"
                                // eslint-disable-next-line @typescript-eslint/no-unused-expressions
                                onChange={formik.handleChange}
                                disabled={loading}
                                required
                                defaultValue={data.data.lastname ? data.data.lastname : ''}
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            />
                        </div>
                    </div>
                    <div className="col-span-full">
                        <div className="flex justify-between">
                            <label htmlFor="street-address" className="block text-sm font-medium leading-6 text-gray-900">
                        Society
                            </label>
                            <span className="text-sm leading-6 text-gray-500" id="society-optional">
          Optional
                            </span>
                        </div>

                        <div className="mt-2">
                            <input
                                id="society"
                                name="society"
                                type="text"
                                defaultValue={data.data.society}
                                onChange={formik.handleChange}
                                disabled={loading}
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            />
                        </div>
                    </div>
                    <div className=" sm:col-span-3">
                        <label htmlFor="country" className="block text-sm font-medium leading-6 text-gray-900">
                Country
                        </label>
                        <div className="mt-2">
                            <CountryDropdown
                                id="country"
                                name="country"
                                value={country}
                                onChange={handleCountryChange}
                                disabled={loading}
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"

                            />
                           
                        </div>
                    </div>
                    <div className="sm:col-span-3">
                        <label htmlFor="region" className="block text-sm font-medium leading-6 text-gray-900">
                      State / Province
                        </label>
                        <div className="mt-2">
                            <RegionDropdown
                                id="region"
                                name="region"
                                country={country}
                                value={region}
                                onChange={handleRegionChange}
                                disabled={loading}
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            />
                        </div>
                    </div>
                    <div className="col-span-full">
                        <label htmlFor="street-address" className="block text-sm font-medium leading-6 text-gray-900">
                Street address
                        </label>
                        <div className="mt-2">
                            <input
                                type="text"
                                name="address"
                                id="address"
                                onChange={formik.handleChange}
                                disabled={loading}
                                required
                                defaultValue={data.data.address ? data.data.address : ''}
                                autoComplete="street-address"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            />
                        </div>
                    </div>
                  
                
                    <div className="sm:col-span-3 sm:col-start-1">
                        <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
                City
                        </label>
                        <div className="mt-2">
                            <input
                                type="text"
                                name="city"
                                id="city"
                                defaultValue={data.data.city ? data.data.city : ''}
                                onChange={formik.handleChange}
                                disabled={loading}
                                autoComplete="address-level2"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            />
                        </div>
                    </div>



                    <div className="sm:col-span-3">
                        <label htmlFor="postalcode" className="block text-sm font-medium leading-6 text-gray-900">
                ZIP / Postal code
                        </label>
                        <div className="mt-2">
                            <input
                                type="text"
                                name="postalcode"
                                id="postalcode"
                                defaultValue={data.data.postal_code ? data.data.postal_code : ''}
                                // eslint-disable-next-line @typescript-eslint/no-unused-expressions
                                onChange={formik.handleChange}
                                disabled={loading}
                                autoComplete="postal-code"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            />
                        </div>
                    </div>
                </div>
            </div>
            <div className="flex items-center justify-end gap-x-6 border-t border-gray-900/10 px-4 py-4 sm:px-8">
                <button type="button" className="text-sm font-semibold leading-6 text-gray-900">
      Cancel
                </button>
                <button
                    type="submit"
                    disabled={
                        loading || formik.errors.postalcode
                            ? true
                            : formik.errors.address
                                ? true
                                : formik.errors.city
                                    ? true
                                    : formik.errors.firstname
                                        ? true
                                        : !!formik.errors.lastname
                    }
                    className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                >
      Save
                </button>
            </div>
        </form>

    );
}
